export function tabs() {
    const tabWrapper = document.querySelectorAll('.js-tabWrapper');
    if (tabWrapper.length > 0) {
        tabWrapper.forEach(function (collection) {
            const tabs = collection.querySelectorAll('.js-tabItem');
            tabs.forEach(function (elem) {
                elem.addEventListener("click", function() {
                    const tabId = elem.getAttribute('data-tab');
                    const parentTab = elem.closest('.js-tabWrapper');
                    const tabContents = parentTab.querySelectorAll('.js-tabContent');
                    const parentTabs = parentTab.querySelectorAll('.js-tabItem');
                    
                    parentTabs.forEach(function(tab) {
                        if(tab != elem) {
                            tab.classList.remove('show');
                        }
                    });
                    elem.classList.add('show');
                    
                    tabContents.forEach(function(content) {
                        if (content.getAttribute('data-tab') === tabId) {
                            content.classList.add('show');
                        } else {
                            content.classList.remove('show');
                        }
                    });
                });
            });
        });
    }
}