function checkInputValidity(input, regexp) {
    let value = input.value;
    if (!regexp.test(value)) {
        input.classList.add('error');
        let errorElement = input.parentNode.querySelector('.js-inputError');
        if (errorElement) {
            errorElement.style.display = 'block';
        }
    } else {
        let errorElement = input.parentNode.querySelector('.js-inputError');
        if (errorElement) {
            errorElement.style.display = 'none';
        }
        input.classList.remove('error');
    }
}

let forms = document.querySelectorAll('.js-form');
forms.forEach(form => {
    let buttons = form.querySelectorAll('.js-formCheck');
    let inputEyeElements = form.querySelectorAll('.js-inputEye');
    buttons.forEach(button => {
        button.addEventListener('click', function (event) {
            let inputNickname = form.querySelectorAll('.js-nicknameCheck');
            let inputRequired = form.querySelectorAll('.js-emptyCheck');
            let inputEmail = form.querySelectorAll('.js-emailCheck');
            let repeatPass = form.querySelector('.js-repeatPass');

            inputNickname.forEach(input => {
                checkInputValidity(input, /^[a-zA-Z]{6,32}$/);
                if (input.value.includes(' ') || /[@~!#$%^&*()+`'";:<>\/\\|]/.test(input.value)) {
                    checkInputValidity(input, /^\S*$/);
                }
            });

            inputRequired.forEach(input => {
                checkInputValidity(input, /^.+/);
            });

            inputEmail.forEach(input => {
                checkInputValidity(input, /^\w+@[a-zA-Z_]+\.[a-zA-Z]{2,3}$/);
            });

            if (repeatPass) {
                let pass = form.querySelector('.js-pass').value;
                let errorElement = repeatPass.parentNode.querySelector('.js-inputError');
                if (repeatPass.value != pass) {
                    repeatPass.classList.add('error');
                    errorElement.style.display = 'block';
                } else {
                    errorElement.style.display = 'none';
                    repeatPass.classList.remove('error');
                }
            }
            if (button.hasAttribute('data-popup') && form.querySelectorAll('.error').length == 0) {
                button.classList.add('js-popupOpen');
            } else {
                button.classList.remove('js-popupOpen');
            }
        });
    });
    inputEyeElements.forEach(function (inputEyeElement) {
        inputEyeElement.addEventListener('click', function () {
            inputEyeElements.forEach(function (elem) {
                elem.classList.toggle('active');
                let inputPass = elem.parentNode.querySelector('input')
                if (elem.classList.contains('active')) {
                    inputPass.type = "text";
                } else {
                    inputPass.type = "password";
                }
            })
        })
    });
    form.addEventListener('input', function (event) {
        if (event.target.classList.contains('js-inputNumbers')) {
            const inputValue = event.target.value;
            const regex = /^\d+$/; 
            if (!regex.test(inputValue)) {
                event.target.value = inputValue.replace(/\D/g, ''); 
            }
        }
    });
});