// Mobile menu
let burger = document.querySelector('.js-burgerBtn');
let menu = document.querySelector('.js-mobileMenu');
burger.addEventListener('click', function () {
  menu.classList.toggle('active');

  let bodyOverflow = window.getComputedStyle(document.body).overflowY;
  document.body.style.overflowY = bodyOverflow === 'hidden' ? 'auto' : 'hidden';
});
// Menu dropdowns
let menuMobileDropdowns = document.querySelectorAll('.js-dropdownMenu');
menuMobileDropdowns.forEach(function (arrow) {
  arrow.addEventListener('click', function () {
    let parent = arrow.parentNode;
    parent.classList.toggle('active');
  });
});

// Lang block
let openLang = document.querySelector('.js-openLang');
let closeLang = document.querySelector('.js-closeLang');
let mobileLang = document.querySelector('.js-mobileLang');
openLang.addEventListener('click', function () {
  // mobile
  if (window.innerWidth < 768) {
    mobileLang.classList.toggle('active');

    let bodyOverflow = window.getComputedStyle(document.body).overflowY;
    document.body.style.overflowY = bodyOverflow === 'hidden' ? 'auto' : 'hidden';
  } else { // tablet
    if (!window.matchMedia('(hover: hover) and (pointer: fine)').matches) {
      mobileLang.classList.toggle('active');
    }
  }
});
closeLang.addEventListener('click', function () {
  mobileLang.classList.remove('active');
  document.body.style.overflowY = 'auto';
});
// desktop
if (window.matchMedia('(hover: hover) and (pointer: fine)').matches) {
  let isMouseOverMobileLang = false;
  openLang.addEventListener('mouseenter', function() {
    if (profileBlock.classList.contains('active')) {
      profileBlock.classList.remove('active');
    }
    mobileLang.classList.add('active');
  });
  openLang.addEventListener('mouseleave', function() {
    setTimeout(function() {
      if (!isMouseOverMobileLang) {
        mobileLang.classList.remove('active');
      }
    }, 100);
  });
  mobileLang.addEventListener('mouseenter', function() {
    isMouseOverMobileLang = true;
    mobileLang.classList.add('active');
  });
  mobileLang.addEventListener('mouseleave', function() {
    isMouseOverMobileLang = false;
    mobileLang.classList.remove('active');
  });
}

// Select lang items
let mobileLangItems = document.querySelectorAll('.js-mobileLangItem');
mobileLangItems.forEach(function (lang) {
  lang.addEventListener('click', function () {
    mobileLangItems.forEach(function (item) {
      item.classList.remove('active');
    });

    lang.classList.add('active');
  });
});

// Profile block
let openProfile = document.querySelector('.js-openProfile');
let profileBlock = document.querySelector('.js-header-profile');
openProfile.addEventListener('click', function () {
  if (!window.matchMedia('(hover: hover) and (pointer: fine)').matches) {
    profileBlock.classList.toggle('active');
  }
});
// desktop
if (window.matchMedia('(hover: hover) and (pointer: fine)').matches) {
  let isMouseOverProfileBlock = false;
  openProfile.addEventListener('mouseenter', function() {
    if (mobileLang.classList.contains('active')) {
      mobileLang.classList.remove('active');
    }
    profileBlock.classList.add('active');
  });
  openProfile.addEventListener('mouseleave', function() {
    setTimeout(function() {
      if (!isMouseOverProfileBlock) {
        profileBlock.classList.remove('active');
      }
    }, 100);
  });
  profileBlock.addEventListener('mouseenter', function() {
    isMouseOverProfileBlock = true;
    profileBlock.classList.add('active');
  });
  profileBlock.addEventListener('mouseleave', function() {
    isMouseOverProfileBlock = false;
    profileBlock.classList.remove('active');
  });
}